/**
 * Provide type safety for environment configuration. The actual configuration is in assets/environment.js.
 * This allows operations to easily override the configuration by replacing the assets/environment.js
 */
export interface Environment {
  /**
   * ob der production mode von Angular aktiviert werden soll -
   * typischerweise false in der entwicklung, und true in allen anderen Teststufen
   */
  production: boolean;

  /**
   * Standardmssig aktive Sprache
   */
  defaultLanguage: string;
}

// augment the Window interface to include the Environment
// see https://mariusschulz.com/blog/declaring-global-variables-in-typescript#augmenting-the-window-interface
declare global {
  interface Window {
    environment: Environment;
  }
}
// read environment config from the 'environment' global set by assets/environment.js
export const environment: Environment = window.environment;
